.burger {
  @include hight-tablet {
    @include burger;
  }

}

.burger-close {
  @include hight-tablet {
    display: block;
    position: absolute;
    right: 10px;
    top: 25px;
    width: 44px;
    height: 44px;
    background-image: url("../img/close.svg");
    background-position: center;
    background-size: 30px 28px;
    background-repeat: no-repeat;
  }

  @include small-mobile {
    top: 10px;
  }
}
