html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

p {
  margin: 0;
}

.page {
  height: 100%;
  font-family: var(--font-family, sans-serif);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
  overflow-anchor: none;
}

img {
  display: block;
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden; // если используете на сайте position: sticky - уберите эту настройку
}

.is-hidden {
  display: none !important;
  /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

// Arrow swipper
.ellipse {
  display: block;
  width: 35px;
  height: 35px;
  border-radius: 100%;
  background-color: var(--color-light);
  filter: drop-shadow(0px 5px 20px rgba(0, 0, 0, 0.13));
  transition: background-color 0.3s ease-in-out;

  svg {
    width: 12px;
    height: 12px;
    fill: var(--color-black);
    transition: fill 0.3s ease-in-out;
  }

  &:hover {
    background-color: var(--color-radical);
    transition: background-color 0.3s ease-in-out;

    svg {
      fill: var(--color-light);
      transition: fill 0.3s ease-in-out;
    }
  }
}

.swiper-friends-prev {
  position: absolute;
  left: -60px;
}

.swiper-friends-next {
  position: absolute;
  right: -60px;
}

// Scrollbar swipper

.swiper-scrollbar-drag {
  position: absolute;
  top: -2px;
  width: 67px;
  height: 11px;
  background-color: #7e7e7e;
  border-radius: 30px;
}

// Title
.title {
  font-weight: 900;
  font-size: 30px;
  line-height: 49px;

  color: var(--color-black);

  @include small-mobile {
    font-size: 27px;
    line-height: 37px;
  }
}
