.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 1;
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;

  &__container {
    @include flex-all-sb;
  }

  &__logo {
    padding-top: 10px;

    @include small-mobile {
      max-width: 110px;
    }
  }

  &__contact {
    display: flex;
    align-items: center;

    &--tell {
      margin-right: 85px;

      &__link {
        position: relative;
        font-weight: 700;
        font-size: 15px;
        line-height: 18px;

        color: #000000;

        &::before {
          content: "";
          display: block;
          position: absolute;
          left: -22px;
          top: 0;
          width: 17px;
          height: 17px;
          background-image: url("../img/phone.svg");
          cursor: pointer;
        }

        @include mobile {
          display: none;
        }
      }

      @include mobile {
        display: none;
      }
    }
  }
}
