// если вы хотите использовать sass-переменные - удалите root
// colors
:root {
  // base
  --font-family: "Montserrat", sans-serif;
  --content-width: 1170px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --color-light: #fff;
  --color-black: #000000;
  --color-radical: #f52557;
  --color-amaranth: #ed1c4e;
  --color-saffron: #f4cb38;
  --color-saffronX: #f4a938;
  --color-linen: #fbf6ed;
  --color-heliotrope: #9250fe;
  --color-palma: #32b71d;
}