.btn {
  background-color: var(--color-light);

  box-shadow: 0px 3px 30px rgba(169, 169, 169, 0.15);
  border-radius: 100px;

  &--active {
    background: linear-gradient(97.27deg, var(--color-radical) 21.72%, var(--color-amaranth) 76.11%);
    border-radius: 110px;
    text-align: center;

    color: var(--color-light);

    &:hover {
      color: var(--color-light);
    }
  }

  &__shadow {
    padding: 25px 28px;
    box-shadow: 0px 9px 35px rgba(241, 33, 83, 0.56);
  }
}
