.card {
  padding: 34px 25px 44px 42px;
  background-color: var(--color-light);
  border-radius: 20px;
  max-width: 290px;

  &__info {
    @include flex-all-sb;
    margin-bottom: 26px;
  }

  &__description {
    margin-top: 21px;
    color: var(--color-black);

    &--nomination {
      margin-bottom: 25px;
      font-weight: 400;
      font-size: 14px;
      line-height: 21px;
    }
  }

  &__boxprice {
    @include flex-all-sb;

    svg {
      width: 29px;
      height: 29px;
      fill: var(--color-radical);
      transition: fill 0.3s ease-in-out;
    }
  }

  &__img {
    img {
      @include mobile {
        margin: 0 auto;
      }
    }
  }

  &__title {
    margin-bottom: 15px;
    font-weight: 700;
    font-size: 19px;
    line-height: 20px;
  }

  &__descr {
    margin-bottom: 21px;
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
  }

  &__price {
    font-weight: 700;
    font-size: 22px;
    line-height: 21px;

    color: var(--color-palma);
  }

  &__button:disabled {
    svg {
      fill: var(--color-saffron);
      transition: fill 0.3s ease-in-out;
    }
  }

  &--visible {
    display: block;
  }

  @include mobile {
    max-width: 100%;
  }
}

.name {
  font-weight: 400;
  font-size: 13px;
  line-height: 18px;

  color: var(--color-black);
}

.circle {
  position: relative;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  margin-right: 18px;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 1px solid #CACACA;
    border-radius: 100%;
    width: 30px;
    height: 30px;

    @include small-mobile {
      width: 20px;
      height: 20px;
    }
  }
}

.dot {
  position: relative;
  margin-left: 8px;
  text-transform: uppercase;

  &:not(:last-child) {
    margin-right: 16px;
  }

  &::before {
    content: "";
    position: absolute;
    top: 4px;
    left: -16px;
    display: block;
    width: 7px;
    height: 7px;
    border-radius: 100%;
  }
}

.saffron {
  &::before {
    background-color: var(--color-saffron);
  }
}

.heliotrope {
  &::before {
    background-color: var(--color-heliotrope);
  }
}