.nav {
  margin-top: 15px;

  &__list {
    @include flex-v-center;

    @include hight-tablet {
      @include flex-all-center;
      flex-direction: column;

      width: 100%;
      height: 100vh;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 43px;
    }

    @include hight-tablet {
      margin-right: 0 !important;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  &__link {
    position: relative;
    font-weight: 400;
    font-size: 15px;
    line-height: 18px;

    color: var(--color-black);
    transition: font-weight 0.3s ease-in-out;

    &::after {
      content: "";
      display: block;
      position: absolute;
      right: 0;
      bottom: -3px;
      width: 0;
      height: 1px;
      background-color: var(--color-black);
      transition: width 0.3s ease-in-out;
    }

    &:hover {
      font-weight: 700;
      transition: font-weight 0.3s ease-in-out;

      &::after {
        content: "";
        left: 0;
        width: 100%;
        transition: width 0.3s ease-in-out;
      }
    }

    @include hight-tablet {
      font-size: 30px;
      line-height: 39px;
    }
  }

  @include hight-tablet {
    position: fixed;
    left: 0;
    top: -15px;
    z-index: 500;
    overflow-y: auto;
    width: 100%;
    height: 100vh;
    background-color: var(--color-saffron);
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
  }
}

.menu--active {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.active {
  position: relative;
  font-weight: 700;

  &::after {
    content: "";
    position: absolute;
    display: block;
    width: 100%;
    height: 1px;
    background-color: var(--color-black);
  }
}
