.friends {
  padding-bottom: 100px;

  &-swiper-slider {
    margin-top: -145px;
    margin-bottom: 100px;
    position: relative;
    max-width: 454px;
    z-index: 0;

    @include hight-desktop {
      margin-left: 55px;
    }

    @include tablet {
      margin: -130px auto 50px;
      // margin-bottom: 60px;
    }

    @include mobile {
      margin: -130px auto 70px;
    }
  }

  &__slider {
    margin: 0;
    max-width: 454px;
    min-height: 270px;

    @include mobile {
      max-width: 293px;
    }
  }

  &__slide {
    max-width: 147px;
    padding: 19px 34px 37px 31px;
    background: var(--color-light);
    box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.05);
    border-radius: 16px;

    picture {
      img {
        margin: 0 auto;
      }
    }

    @include mobile {
      max-width: 225px;
      margin-right: 15px !important;
    }

    @include small-mobile {
      margin-right: 10px !important;
    }
  }

  &__btns {
    @include flex-all-sb;

    position: absolute;
    top: 40%;
    z-index: 1;
    width: 100%;

    transform: translateY(-50%);

    @include mobile {
      display: none;
    }
  }

  &__scroll {
    position: absolute;
    left: 50%;
    bottom: 20px;
    transform: translateX(-50%);
    background-color: #e1e1e1;
    border-radius: 20px;
    width: 270px;
    height: 6px;
  }

  &__subtitle {
    @include flex-v-center;

    img {
      width: 13.5px;
      height: 9px;
    }
  }

  &__link {
    margin-right: 8px;
    position: relative;
    display: block;
    font-weight: 700;
    font-size: 15px;
    line-height: 18px;

    cursor: pointer;

    &--dog {
      color: #f07530;
    }

    &--parrot {
      color: var(--color-palma);
    }

    &--cat {
      color: var(--color-heliotrope);
    }

    &--jocko {
      color: #ed1c4e;
    }

    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: 1;
    }

    @include small-mobile {
      @include flex-all-center;
    }
  }

  &__title {
    margin-bottom: 68px;
    width: 371px;
    height: 98px;

    @include small-mobile {
      margin-bottom: 25px;
    }
  }

  &__list {
    @include flex-all-sb;

    @include small-mobile {
      flex-wrap: wrap;
    }
  }

  &__item {
    @include flex-v-center;

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;

      &:not(:last-child) {
        margin-right: 10px;
      }
    }

    @include small-mobile {
      margin-right: 0 !important;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  &__image {
    margin-right: 27px;

    @include mobile {
      margin-right: 0;
      margin-bottom: 15px;
    }
  }

  &__content {
    max-width: 250px;

    &--subtitle {
      margin-bottom: 6px;
      font-weight: 900;
      font-size: 20px;
      line-height: 23px;

      color: var(--color-black);

      @include mobile {
        text-align: center;
      }
    }

    &--descr {
      font-weight: 400;
      font-size: 16px;
      line-height: 20px;

      color: var(--color-black);

      @include mobile {
        text-align: center;
      }
    }

    @include mobile {
      max-width: 100%;
    }
  }
}
