.form {
  position: relative;
  max-width: 770px;
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  &__label {
    display: flex;
    flex-direction: column-reverse;

    &:not(:last-child) {
      margin-bottom: 50px;

      &:nth-child(2n + 1) {
        margin-right: 30px;
      }
    }
  }

  &__input {
    padding-bottom: 20px;
    border-bottom: 1px solid var(--color-light);
    background-color: inherit;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    color: var(--color-light);

    &::placeholder {
      font-weight: 500;
      font-size: 18px;
      line-height: 22px;
      color: var(--color-light);
      opacity: 0.6;
    }

    &:focus {
      outline: 0;
    }
  }

  &__textarea {
    min-height: 140px;
    grid-column: 1 / 3;
    resize: none;
    overflow: auto;
    border: none;
    border-bottom: 1px solid var(--color-light);
  }

  &__btn {
    position: absolute;
    right: -70px;
    bottom: 0;
    width: 48px;
    height: 48px;
    background-color: var(--color-radical);
    border-radius: 100%;

    svg {
      width: 25px;
      height: 18px;
      fill: var(--color-light);
    }

    @include mobile {
      right: 10px;
    }
  }

  @include small-tablet {
    max-width: 600px;
  }

  @include mobile {
    display: flex;
    flex-wrap: wrap;
  }
}

.just-validate-error-label {
  margin-bottom: 3px !important;
  color: var(--color-saffron) !important;
}
