.catalog {
  padding-top: 100px;
  padding-bottom: 155px;
  background-color: var(--color-linen);

  &__bottom {
    position: relative;

    &-item {
      display: none;
    }
  }

  &__card {
    z-index: 0;
  }

  &__title {
    margin-bottom: 50px;
  }

  &__list {
    @include flex-v-center;

    margin-bottom: 69px;

    @include tablet {
      margin-bottom: 50px;
    }

    @include mobile {
      display: grid;
      grid-template-columns: repeat(3, 1fr);
    }

    @include small-mobile {
      display: grid;
      grid-template-columns: repeat(1, 1fr);
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 22px;

      @include small-tablet {
        margin-right: 10px;
      }

      @include mobile {
        margin-right: 0;
      }
    }

    &:not(:nth-last-child(-n+3)) {
      margin-bottom: 10px;
    }

    &:not(:nth-child(3n+3)) {
      margin-right: 10px;
    }

    @include small-mobile {
      margin-right: 0 !important;

      &:not(:last-child) {
        margin-bottom: 10px;
      }
    }
  }

  &__btns {
    @include flex-all-sb;

    position: absolute;
    top: 50%;
    z-index: 1;
    width: 100%;

    transform: translateY(-50%);

    @include hight-desktop {
      display: none;
    }
  }

  &__btn {
    padding: 16px 49px;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;

    color: #777777;
    transition: color 0.3s ease-in-out;

    background-color: var(--color-light);
    box-shadow: 0px 3px 30px rgba(169, 169, 169, 0.15);
    border-radius: 100px;

    &:hover {
      color: var(--color-radical);
      transition: color 0.3s ease-in-out;
    }

    @include tablet {
      padding: 15px 40px;
    }

    @include small-tablet {
      padding: 10px 30px;
    }

    @include mobile {
      width: 100%;
    }
  }
}

.swiper-catalog-prev {
  position: absolute;
  left: -60px;
}

.swiper-catalog-next {
  position: absolute;
  right: -60px;
}

// Pagination
.catalog-pagination {
  position: absolute;
  bottom: -80px !important;
  z-index: 0;
}

.swiper-pagination-bullet {
  width: 16px;
  height: 16px;

  &-active {
    background: rgba(0, 0, 0, 0.69);
  }
}
