.copyright {
  background-color: var(--color-black);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    @include mid-mobile {
      max-width: 160px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 14px;
    line-height: 17px;
    color: var(--color-light);

    @include mid-mobile {
      display: none;
    }
  }

  &__svg {
    svg {
      width: 20px;
      height: 30px;
      rotate: -90deg;
      fill: var(--color-light);
      cursor: pointer;
    }

    @include mid-mobile {
      display: none;
    }
  }

  &--tell {
    margin-right: 85px;

    &__link {
      position: relative;
      font-weight: 700;
      font-size: 15px;
      line-height: 18px;

      color: var(--color-light);

      &::before {
        content: "";
        display: block;
        position: absolute;
        left: -22px;
        top: 0;
        width: 17px;
        height: 17px;
        background-image: url("../img/svg/phone.svg");
        cursor: pointer;
      }
    }

    @include mid-mobile {
      margin-right: 0;
    }
  }
}
