.about {
  padding-top: 100px;
  padding-bottom: 120px;
  background-color: var(--color-light);

  &__title {
    &:not(:last-child) {
      margin-bottom: 26px;
    }
  }

  &__picture {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-template-rows: repeat(6, 5vw);
    gap: 30px;

    margin-bottom: 100px;
    @include small-mobile {
      grid-template-rows: repeat(8, 5vw);
      gap: 20px;

      margin-bottom: 70px;
    }
  }

  &__image {
    filter: drop-shadow(0px 5px 30px rgba(56, 137, 202, 0.3));
    transition: transform 0.3s ease-in-out;

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 5px;
    }

    &:hover {
      position: relative;
      transform: scale(1.06);
      transition: transform 0.3s ease-in-out;

      cursor: pointer;

      &::before {
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 1;
        transform: translate(-50%, -50%);
        width: 57px;
        height: 57px;
        background-color: rgba(217, 217, 217, 0.5);
        border-radius: 100%;
      }

      &::after {
        content: "";
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        z-index: 2;
        transform: translate(-50%, -50%);
        width: 23px;
        height: 23px;
        background-image: url("../img/search.svg");
      }
    }
  }

  &__left {
    max-width: 443px;

    @include small-tablet {
      max-width: 100%;
    }
  }

  &__rigth {
    @include small-tablet {
      display: none;
    }
  }

  &__box {
    @include flex-all-sb;
  }

  &__subtitle {
    margin-bottom: 27px;
    font-weight: 900;
    font-size: 20px;
    line-height: 24px;

    color: var(--color-black);
  }

  &__text {
    margin-bottom: 27px;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;

    color: var(--color-black);

    p {
      &:not(:last-child) {
        margin-bottom: 30px;
      }
    }
  }

  &__link {
    position: relative;
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;

    color: #f52456;

    &::after {
      content: "";
      display: block;
      position: absolute;
      right: -20px;
      top: 5px;
      width: 13px;
      height: 9px;
      background-image: url("../img/arrow-link.svg");
    }
  }
}

.picture-1 {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 1;
  grid-row-end: 3;

  @include small-mobile {
    grid-column-end: 4;
  }
}

.picture-2 {
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 1;
  grid-row-end: 5;

  @include small-mobile {
    grid-column-start: 4;
    grid-column-end: 7;
  }
}

.picture-3 {
  grid-column-start: 5;
  grid-column-end: 7;
  grid-row-start: 1;
  grid-row-end: 4;

  @include small-mobile {
    grid-column-start: 1;
    grid-column-end: 4;
    grid-row-start: 7;
    grid-row-end: 9;
  }
}

.picture-4 {
  grid-column-start: 1;
  grid-column-end: 3;
  grid-row-start: 3;
  grid-row-end: 7;

  @include small-mobile {
    grid-column-end: 4;
  }
}

.picture-5 {
  grid-column-start: 3;
  grid-column-end: 5;
  grid-row-start: 5;
  grid-row-end: 7;

  @include small-mobile {
    grid-column-start: 4;
    grid-column-end: 7;
  }
}

.picture-6 {
  grid-column-start: 5;
  grid-column-end: 7;
  grid-row-start: 4;
  grid-row-end: 7;

  @include small-mobile {
    grid-column-start: 4;
    grid-column-end: 7;
    grid-row-start: 7;
    grid-row-end: 9;
  }
}
