.footer {
  position: relative;
  padding-top: 90px;
  padding-bottom: 160px;
  background-color: var(--color-black);

  &__container {
    position: relative;

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 50px;
      left: 45%;
      width: 751px;
      height: 751px;
      transform: translateX(-50%);
      background-image: url("../img/footer-1.png");

      @include tablet {
        display: none;
      }
    }
  }

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    width: 560px;
    height: 847px;
    background-image: url("../img/footer-2.png");

    @include small-tablet {
      width: 500px;
    }

    @include small-mobile {
      height: 100vh;
    }
  }

  &__title {
    margin-bottom: 80px;
    font-size: 30px;
    color: var(--color-light);
  }

  &__checkbox {
    margin-top: 80px;
    display: flex;
    flex-direction: column;
  }

  @include small-mobile {
    padding-bottom: 100px;
  }
}

.custom-checkbox {
  @include custom-checkbox;

  &:not(:last-child) {
    margin-bottom: 14px;
  }
}

.custom-checkbox__content {
  font-weight: 500;
  font-size: 14px;
  line-height: 22px;
  color: var(--color-light);
  user-select: none;
}
