.hero {
  position: relative;
  padding-top: 250px;
  min-height: 100vh;
  background: linear-gradient(124.61deg, var(--color-saffron) 13.66%, var(--color-saffronX) 81.62%);

  &__container {
    @include flex;
  }

  &__content {
    max-width: 454px;

    @include tablet {
      margin: 0 auto;
      max-width: 655px;
    }
  }

  &__title {
    margin-bottom: 60px;
    font-weight: 900;
    font-size: 40px;
    line-height: 49px;

    color: var(--color-black);

    &-text {
      margin-top: 23px;
      display: block;
      font-weight: 400;
      font-size: 19px;
      line-height: 27px;

      color: var(--color-black);

      @include tablet {
        display: none;
      }

      @include tablet {
        display: text;
      }
    }

    @include mobile {
      font-size: 25px;
      line-height: 30px;
    }

    @include mid-mobile {
      font-size: 30px;
    }
  }

  &__link {
    font-weight: 600;
    font-size: 17px;
    line-height: 21px;
    text-align: center;

    color: var(--color-light);

    @include tablet {
      display: none;
    }
  }

  &__image {
    position: absolute;
    right: 0;
    bottom: 0;

    @include hight-desktop {
      max-width: 75%;
    }

    @include tablet {
      display: none;
    }
  }

  @include tablet {
    padding-top: 170px;
    min-height: 80vh;
  }
}
