.basket {
  position: relative;
  cursor: pointer;

  &__block {
    position: relative;
    margin-right: 13px;
    border-radius: 100%;
    display: block;
    width: 41px;
    height: 41px;
    background-color: var(--color-black);

    &::before {
      content: "";
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      z-index: 1;
      width: 18px;
      height: 18px;
      transform: translate(-50%, -50%);
      background-image: url("../img/basket.svg");
    }

    @include small-mobile {
      margin-right: 5px;
      width: 35px;
      height: 35px;
    }
  }

  &__price {
    position: relative;

    &::after {
      content: "";
      display: block;
      position: absolute;
      right: -20px;
      top: 5px;
      width: 16px;
      height: 8px;
      background-image: url("../img/down-arrow.svg");

      @include hight-desktop {
        right: -20px;
      }
    }
  }

  &--active {
    &:hover {
      cursor: pointer;

      .basket-content {
        opacity: 1;
        visibility: visible;
        transition: opacity 0.3s, visibility 0.3s;
      }
    }
  }

  @include hight-desktop {
    margin-right: 20px;
  }
}

.basket-content {
  position: absolute;
  right: 0;
  top: 100%;
  width: 450px;
  margin-top: 10px;
  border-radius: 5px;
  background-color: var(--color-light);
  box-shadow: 0px 0px 30px 6px rgba(34, 60, 80, 0.2);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s, visibility 0.3s;

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 24px 20px;
  }

  &__fullprice {
    display: flex;
    flex-direction: column;
    font-weight: 400;
    font-size: 19px;
    line-height: 20px;

    color: var(--color-black);
  }

  &__btn {
    padding: 10px;
  }

  @include mobile {
    position: fixed;
    top: 0;
    right: 0;
    width: 320px;
    margin-top: 60px;
  }
}

.basket-visible {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s, visibility 0.3s
}

.simplebar {
  max-height: 365px;
}

.fullprice {
  margin-top: 5px;
  font-weight: 700;
  font-size: 19px;
  line-height: 20px;

  color: var(--color-black);
}

.basket-product {
  display: flex;
  align-items: flex-end;
  padding: 12px 25px 12px 15px;

  &__img {
    max-width: 100px;
    max-height: 100px;
    object-fit: cover;
    margin-right: 30px;
  }

  &__title {
    max-width: 100px;
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 19px;
    line-height: 20px;

    color: var(--color-black);
  }

  &__price {
    font-weight: 700;
    font-size: 22px;
    line-height: 21px;

    color: var(--color-palma);
  }

  &__delite {
    margin-left: auto;
    width: 29px;
    height: 29px;
    background-image: url("../img/svg/paw.svg");
  }
}

.simplebar-track.simplebar-vertical {
  width: 4px;
  background: rgba(77, 77, 77, 0.1);
  border-radius: 5px;
  right: 5px;
  margin-top: 20px;
  height: calc(100% - 40px);
}

.simplebar-track.simplebar-vertical .simplebar-scrollbar::before {
  background: rgba(77, 77, 77, 0.2);
  border-radius: 5px;
  width: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 1;
}
